import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RolePermissionCrudComponent } from './role-permission-crud/role-permission-crud.component';
import { TABS_DEMO_ROUTES } from './role-permission-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : RolePermissionCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class RolePermissionRoutingModule{}